/* ================ SCROLL DOWN ================ */
.scroll__down {
	position: absolute;
	right: -2.3rem;
	bottom: 5rem;
	transform: rotate(90deg);
	font-weight: 300;
	font-size: 0.9rem;
}

/* ================= MEDIA QUERIES (medium devices) ==================== */
@media screen and (max-width: 1024px) {
	header {
		height: 68vh;
	}
}

/* ================= MEDIA QUERIES (small devices) ==================== */
@media screen and (max-width: 600px) {
	header {
		height:100vh;
	}

	.header__socials,
	.scroll__down {
		display: none;
	}
}




